export const getFloatValue = (string) => {
  let floatVal = string.match(/[+-]?\d+(\.\d+)?/g)[0]
  console.warn(floatVal)
  return (null !== floatVal) ? parseFloat( parseFloat(floatVal).toFixed(2)): ''
} 

